// ===== header ===========================================

.page-header {
        
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: absolute;

    top: 0;
    left: 50%;
    width: 100%;
    max-width: 1280px;
    z-index: 2;

    padding-top: 8px;

    background-color: transparent;

    transform: translateX(-50%);

    @media screen and (min-width: 768px) {
        padding-top: 32px;
        justify-content: flex-start;
        }

    @media screen and (min-width: 1200px) {
        padding: 37px 16px 0 16px;
        }
}

// ===== logo ===================================================

.logo__link {
    display: block;

    height: 50px;
}

.logo__pic {
    width: 199px;
    height: 50px;

    fill: var(--accent-color);
}


// ===== site-nav ======================================================


.site-nav {
    display: none;

    @media screen and (min-width: 1200px) {
        display: block;
    }
}

.site-nav__list {
    display: flex;
    gap: 35px;

    margin-left: 128px;

    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
}

.site-nav__item:hover,
.site-nav__item:focus {
    color: var(--accent-color);
}

.nav__link {
    display: block;

    text-decoration: none;
    cursor: pointer;
    color: var(--dominant-color);
}

.current {
    color: var(--accent-color);
}

.nav__link:hover,
.nav__link:focus {
    color: var(--accent-color);
}

.nav__link:visited {
    color: currentColor;
}

.nav__link:active {
    color: var(--dominant-color);
}

.button__icon {
    margin-left: 12px;
    width: 6px;
    height: 10px;
}

.button--visible {
    display: none;
    margin-left: auto;  

    @media screen and (min-width: 768px) {
        display: block;
    }

    @media screen and (min-width: 1200px) {
        margin-left: auto;
    }

}

// ===== menu button ================================================

.menu__button {
    display: block;

    position: absolute;
    top: 27px;
    right: 15px;
    z-index: 10;
    
    width: 44px;
    height: 44px;
    
    border: 0;
    border-radius: 0;

    background-color: transparent;
    cursor: pointer;

    @media screen and (min-width: 768px) {
    top: 46px;
    right: 214px;

    }
    @media screen and (min-width: 1200px) {
        display: none; 
        
    }
    &.is-open {
        @media screen and (min-width:768px) {
            top: 20px;
            right: 32px;
        }
    }
}
 
// .menu__burger {
//     transform: translateX(2px);
//     transform: translateY(20px);
// }

.menu__close {
    display: none;
    transform: translateX(28%);
}

.menu__button .menu__close {
    position: absolute;
    display: none;
}

.menu__button.is-open .menu__close {
    display: block;
    position: fixed;

}

.menu__button.is-open .menu__burger {
    display: none;
}
    
// ===== mob menu ============================================== 

.mobile-menu {

    display: none;

    position: fixed;    
    top: 0;
    right: 0;
    z-index: 6;

    padding: 60px 20;

    width: 250px;
    height: 100%;

    background-color: var(--modal-window-color);

    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;

    // @media screen and (min-width: 768px) {
    //     // width: 254px;
    //     width: 63%;
    // }

    &.is-open {
        display: block;
    }

}

.button--mobile {
    width: 209px;
    margin-bottom: 33px;
    margin-left: auto;
    margin-right: auto;
    
    @media screen and (min-width: 768px) {
        width: 186px;
        margin-left: 0;
    }

}

.button--mobile > .button__icon {
    margin-left: 21px;
}

.mobile-nav > .nav__item {
    margin-bottom: 23px;
}

// ===== hero ============================================== 

.hero {

    position: relative;
    min-width: 320px;
    max-width: 1280px;
    width: 100%;

    height: 502px;

    overflow: hidden;

    background-color: var(--item-pink-color);


    @media screen and (min-width: 768px) {
        height: 416px;
    }

    @media screen and (min-width: 1200px) {
        height: 664px;
    }

}

// ===== hero pictures ===================================

.hero__circle {

    display: block;
    
    position: absolute;
    top: 83px;
    left: 36px;
    z-index: 1;

    width: 346px;
    height: 344px;

    background-color: var(--accent-secondary-color);
    border-radius: 50%;

    @media screen and (min-width: 768px) {
        top: 43px;
        left: 248px;
        width: 346px;
        height: 344px;
    }
    
    @media screen and (min-width: 1200px) {
        top: 54px;
        left: 517px;
        width: 534px;
        height: 534px;
    }

}

.hero__icecream {

    display: block;

    position: absolute;
    
    bottom: 0;
    left: 117px;
    z-index: 3;

    @media screen and (min-width: 768px) {
        left: 332px;
        width: 143px;
    
    }
    @media screen and (min-width: 1200px) {
        left: 636px;
        width: 236px;
    }

} 

.hero__milk {
    display: none;

    position: absolute;
    z-index: 3;

    @media screen and (min-width: 768px) {
        display: block;
        bottom: 16px;
        left: 32px;
        }
        
    @media screen and (min-width: 1200px) {
        bottom: 60px;
        left: 16px;

        width: 213px;
        }
}

.hero__girl {
    display: none;

    position: absolute;
    z-index: 3;

    @media screen and (min-width: 768px) {
        display: block;
        bottom: 0;
        right: 32px;
        }
    
        @media screen and (min-width: 1200px) {
        right: 16px;
    
        width: 304px;
        }
}


// ===== hero text =========================================

.hero__title--main {
    
    position: absolute;
    z-index: 4;
    top: 194px;
    left: 20px;

    @media screen and (min-width: 768px) {
        top: 102px;
        left: 32px;
    }
    @media screen and (min-width: 1200px) {
        top: 147px;
        left: 16px;
    }

}

.hero__title {
    margin-bottom: 20px;

    font-family: var(--secondary-font);
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.15;
    text-transform: uppercase;

    color: var(--dominant-color);

    @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 1.14;
    }
    @media screen and (min-width: 1200px) {
    font-size: 38px;
    line-height: 44px;
    margin-bottom: 24px;
    }
}

.hero__title--accent {

    font-size: 48px;
    line-height: 0.96;
    text-transform: uppercase;

    color: var(--accent-color);

    @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: 0.91;
    }
    @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 0.9;
    }
}

.hero__buttons {
    display: flex;
    gap: 8px;
    
    @media screen and (min-width: 1200px) {
        gap: 12px;
    }
}

.button--small {
    display: block;

    padding: 7px 15px;
    
    height: 24px;

    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;

    text-decoration: none;

    @media screen and (min-width: 1200px) {
    padding: 10px 25px;

        height: 40px;

        font-size: 14px;
        line-height: 1.29;
    }
}

.button--small:visited {
    background-color: var(--dominant-color);
}

.button--inverse:visited {
    background-color: var(--accent-color);
}

.hero__arrow-circle {
    display: none;
        
    width: 18px;
    height: 18px;
    border-radius: 50%;
    
    background-color: var(--dominant-color);
    
    cursor: pointer;
    
    @media screen and (min-width: 768px) {
        display: block;
        
        position: absolute;
        bottom: 56px;
        left: 184px;
    }
        
    @media screen and (min-width: 1200px) {
        top: 554px;
        left: 229px;
        
        width: 26px;
        height: 26px;
    }

}

.hero__arrow {
    position: absolute;
    left: 5px;
    
    width: 18px;
    height: 18px;

    fill: var(--accent-color);

    @media screen and (min-width: 1200px) {
    top: 4px;
    left: 8px;
    }
}

.hero__info {
    display: none;

    position: absolute;
        top: 92px;
        right: 20px;
        z-index: 1;

    font-family: var(--secondary-font);
        font-weight: 400;
        font-size: 28px;
        line-height: 1.14;
        text-align: right;
        
        color: var(--accent-color);
    
    @media screen and (min-width: 768px) {
        display: block;
        right: 40px;
        
    }
        
    @media screen and (min-width: 1200px) {
        top: 156px;
        right: 34px;
        
        font-size: 46px;
        line-height: 1.15;
    }


    &::before {
        content: '';

        position: absolute;
        top: -2px;
        right: -8px;
        z-index: -1;

        width: 16px;
        height: 16px;
        border-radius: 50%;

        
        background-color: var(--dominant-color);
        @media screen and (min-width: 1200px) {
            top: -4px;
            right: -18px;
            width: 30px;
            height: 30px;
        
            }
    }
}

.hero__text-right {
    font-family: var(--dominant-font);
    font-size: 8px;
    line-height: 10px;
    text-align: right;

    color: var(--dominant-color);
    @media screen and (min-width: 1200px) {
        font-size: 14px;
        line-height: 1.29;

    }
}

.hero__info--23 {
    top: 152px;

    @media screen and (min-width: 1200px) {
    top: 244px;
    }

}

.hero__text {
    display: none;

    position: absolute;
    z-index: 3;

    width: 124px;

    font-weight: 400;
    font-size: 8px;
    line-height: 1.5;
    
    @media screen and (min-width: 768px) {
        display: block;

        top: 287px;
        left: 184;
    }
    
    @media screen and (min-width: 1200px) {
        top: 456px;
        left: 229px;

        width: 200px;
        
        font-size: 14px;
        line-height: 1.43;
        
        
    }

}

